import React from 'react'
import PDF from '../../images/juani-sabbatini-cv.pdf'

export default function PdfCv() {
    document.title = "Juani Sabbatini | CV"
    return (
        <div>
            <embed src={PDF} type="application/pdf" className="pdf-cv" width="100%" height="100%" />
        </div>
    )
}
